import {Button, Col, Container, Form, FormControl, InputGroup, Row} from "react-bootstrap";
import * as React from "react";

interface State {
  visitorName: string
};

interface Props {

}

class Greeter extends React.Component<Props, State> {
  constructor(props) {
    super(props);
    this.state = { visitorName: null };
  }

  render() {
    const {visitorName} = this.state;
    return (
      <>
        <Container>
          <Row className="justify-content-md-center">
            <Col sm="auto">
              <h1> Hello! What's your name? </h1>
            </Col>
          </Row>
          <Form>
            <Form.Row className="justify-content-sm-center">
              <Col sm={6}>
                <InputGroup>
                  <InputGroup.Prepend>
                    <InputGroup.Text>
                      My name is
                    </InputGroup.Text>
                  </InputGroup.Prepend>
                  <FormControl onChange={this.setName}/>
                </InputGroup>
              </Col>
            </Form.Row>
          </Form>
          {
            visitorName && <Row className="justify-content-md-center">
              <Col sm="auto">
                <p> Nice to meet you {visitorName}. My name is Ved! </p>
              </Col>
            </Row>
          }
        </Container>
      </>
    );
  }

  setName = (inputCtrl) => {
    this.setState({visitorName: inputCtrl.target.value});
  }
}

export default Greeter;
